.timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 900px;
  margin: 20px auto;
  margin-top: 30px;
  font-size: 30px;
  background-color: aquamarine;
}

.field {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-left: 30px;
  font-size: 40px;
  color: coral;
  background-color: rgb(43, 29, 102);
}

.field:first-child {
  margin-left: 0px;
}
